import React, { useState, useEffect } from "react";
import bridge from "@vkontakte/vk-bridge";
import {
  View,
  ScreenSpinner,
  TabbarItem,
  Epic,
  Div,
  Tabbar,
  ConfigProvider,
  ActionSheet,
  ActionSheetItem,
  Snackbar,
  Avatar
} from "@vkontakte/vkui";
import "@vkontakte/vkui/dist/vkui.css";

import Icon28MusicMicOutline from "@vkontakte/icons/dist/28/music_mic_outline";
import Icon16Done from "@vkontakte/icons/dist/16/done";
import Icon16Cancel from "@vkontakte/icons/dist/16/cancel";
import Icon16CheckCircle from "@vkontakte/icons/dist/16/check_circle";
import Icon28UserCircleOutline from "@vkontakte/icons/dist/28/user_circle_outline";
import Icon28Favorite from "@vkontakte/icons/dist/28/favorite";
import Icon28MarketOutline from "@vkontakte/icons/dist/28/market_outline";
import Icon28GameOutline from "@vkontakte/icons/dist/28/game_outline";
import Icon24Help from "@vkontakte/icons/dist/24/help";

import "./styles/reset.sass";
import "./styles/button.sass";
import "./styles/icons.sass";
import "./styles/panels.sass";

import { Home, HowItWorks } from "./panels";

const App = () => {
  const [scheme, setScheme] = useState("bright_light");
  const [activePanel, setActivePanel] = useState("home");
  const [activeModal, setActiveModal] = useState(null);
  const [fetchedUser, setUser] = useState(null);
  const [popout, setPopout] = useState(<ScreenSpinner size="large" />);
  const [course, setCourse] = useState(0.20);
  const [online, setOnline] = useState(false);
  const [shisoids, setShisoids] = useState(0);
  const [isBanned, setIsBanned] = useState(false);
  const [getPlatform, setGetPlatform] = useState("");
  const [snackbar, setSnackbar] = useState(null);

  useEffect(() => {
    bridge.subscribe((e) => {
      switch (e.detail.type) {
        case "VKWebAppUpdateConfig":
          const schemeAttribute = document.createAttribute("scheme");
          schemeAttribute.value = e.detail.data.scheme;
          //   ? e.detail.data.scheme
          //   : "bright_light";
          document.body.attributes.setNamedItem(schemeAttribute);

          setScheme(e.detail.data.scheme);
          break;
        // case "VKWebAppViewHide":
        //   socket.close();
        //   setRating(0);
        //   setSocketReconnection(true);

        //   break;
        // case "VKWebAppViewRestore":
        //   socket.open();
        //   setSocketReconnection(false);
        //   break;
      }
    });

    async function fetchData() {
      const user = await bridge.send("VKWebAppGetUserInfo");
      setUser(user);
      setPopout(null);
    }
    fetchData();
  }, []);

  // useEffect(() => {
  //   bridge.subscribe(({ detail: { type, data } }) => {
  //     if (type === "VKWebAppUpdateConfig") {
  //       const schemeAttribute = document.createAttribute("scheme");
  //       schemeAttribute.value = data.scheme ? data.scheme : "client_light";
  //       document.body.attributes.setNamedItem(schemeAttribute);
  //     }
  //   });
  // 	async function fetchData() {
  //     const user = await bridge.send("VKWebAppGetUserInfo");
  //     setUser(user);
  //     setPopout(null);
  //   }
  //   fetchData();
  // }, []);

  useEffect(() => {
    // Определение ОС
    bridge
      .sendPromise("VKWebAppGetClientVersion")
      .then((result) => {
        // console.log("result", result);
        const res = result.platform;
        if (result.platform === "ios") {
          setGetPlatform("ios");
        } else {
          setGetPlatform(result.platform);
        }
      })
      .catch((err) => console.log(err));
  }, []);

  function updateTheme() {
    console.log("updateTheme");

    if (scheme === "bright_light" || scheme === "client_light") {
      setScheme("space_gray");
      bridge.send("VKWebAppSetViewSettings", {
        status_bar_style: "light",
        action_bar_color: "#000",
      });
    } else if (scheme === "space_gray" || scheme === "client_dark") {
      setScheme("bright_light");
      bridge.send("VKWebAppSetViewSettings", {
        status_bar_style: "dark",
        action_bar_color: "#fff",
      });
    }
  }




  function shareLink() {
    const link = `https://vk.com/app7523466#${fetchedUser.id}`;
    bridge.send("VKWebAppShare", {
      link: link,
    });
  }

   // Копирование в буфер
   function copyLink() {
    const link = `https://vk.com/app7523466#${fetchedUser.id}`;
    bridge
      .sendPromise("VKWebAppGetClientVersion")
      .then((result) => {
        console.log(result.platform);
        if (result.platform === "web" || result.platform === "mobile-web") {
          window.navigator.clipboard.writeText(link).then(
            () => {
              console.log("successfully set", link);
              openAlert("Ссылка скопирована");
            },
            () => {
              console.log("write failed", link);
            }
          );
        } else {
          bridge.send("VKWebAppCopyText", { text: link });
          openAlert("Ссылка скопирована");
        }
      })
      .catch((error) => {
        console.log("error", error);
      });
  }

  //  Поделиться в истории
  function story(type) {
    const urlStories =
      "https://sun9-20.userapi.com/c857232/v857232225/1c8d06/oFDYcFtVvC8.jpg";
    const urlExchange =
      "https://sun9-20.userapi.com/c857232/v857232225/1c8d06/oFDYcFtVvC8.jpg";
    const url = `https://vk.com/app7523466#${fetchedUser.id}`;

    bridge.sendPromise("VKWebAppShowStoryBox", {
      background_type: "image",
      url: type === "stories" ? urlStories : urlExchange,
      attachment: {
        text: "go_to",
        type: "url",
        url: url,
      },
    });
  }

  //  ДОБАВЛЕНИЕ РЕПОСТА НА СТЕНУ ПОЛЬЗОВАТЕЛЯ
  function share(e, _type) {
    e.preventDefault();
    const url = `https://vk.com/app7523466#${fetchedUser.id}`;
    const urlPhotoStories = `photo73606509_457264707,https://vk.com/app7523466#${fetchedUser.id}`;
    const urlPhotoExchange = `photo73606509_457264707,https://vk.com/app7523466#${fetchedUser.id}`;
    const textStories = `А ты уже майнишь PornCoin?, их можно продать и вывести на QIWI Кошелёк, Приложение - ${url}`;
    const textExchange = `Купи мои PornCoin на бирже, поддержи мой потенциал, Приложение - ${url}`;

    bridge.send("VKWebAppShowWallPostBox", {
      message: _type === "stories" ? textStories : textExchange,
      attachments: _type === "stories" ? urlPhotoStories : urlPhotoExchange,
    });
  }

  function openPopoutSharing() {
    setActiveModal(null);
    setPopout(
      <>
        <ActionSheet onClose={() => setPopout(null)}>
          <ActionSheetItem autoclose onClick={shareLink}>
            Поделиться ссылкой
          </ActionSheetItem>
          <ActionSheetItem autoclose onClick={copyLink}>
            Скопировать ссылку
          </ActionSheetItem>
          <ActionSheetItem autoclose onClick={() => story("stories")}>
            Поделиться в истории
          </ActionSheetItem>
          <ActionSheetItem autoclose onClick={(e) => share(e, "stories")}>
            Поделиться на стене
          </ActionSheetItem>
        </ActionSheet>
      </>
    );
  }

  function openAlert(text, backgroundColor = "green") {
    if (snackbar) return;
    setSnackbar(
      <Snackbar
        duration="7000"
        layout="horizontal"
        onClose={() => setSnackbar(null)}
        before={
          <Avatar size={24} style={{ backgroundColor }}>
            {backgroundColor === "green" ? (
              <Icon16Done fill="#fff" width={14} height={14} />
            ) : (
              <Icon16Cancel fill="#fff" width={14} height={14} />
            )}
          </Avatar>
        }
      >
        {text}
      </Snackbar>
    );
  }



  const go = (e) => {
    setActivePanel(e.currentTarget.dataset.to);
  };

  return (
    <Epic
      tabbar={
        <>
          {isBanned ? (
            <Div
              style={{
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                flexDirection: "row",
                marginTop: "25px",
              }}
            >
              Вы забанены, по поводу разблокировки обратитесь к администрации
              ДУРКИ
            </Div>
          ) : (
            <Tabbar>
              <TabbarItem onClick={go} data-to="top" text="Топы">
                {activePanel === "top" ? (
                  <Icon28Favorite fill="D40000" />
                ) : (
                  <Icon28Favorite />
                )}
              </TabbarItem>
              <TabbarItem onClick={go} data-to="home" text="Главная">
                {activePanel === "home" ? (
                  <Icon28UserCircleOutline fill="D40000" />
                ) : (
                  <Icon28UserCircleOutline />
                )}
              </TabbarItem>
              <TabbarItem onClick={go} data-to="shop" text="Магазин">
                {activePanel === "shop" ? (
                  <Icon28MarketOutline fill="D40000" />
                ) : (
                  <Icon28MarketOutline />
                )}
              </TabbarItem>
              <TabbarItem onClick={go} data-to="games" text="Игры">
                {activePanel === "games" ? (
                  <Icon28GameOutline fill="D40000" />
                ) : (
                  <Icon28GameOutline />
                )}
              </TabbarItem>
            </Tabbar>
          )}
        </>
      }
    >
      <ConfigProvider isWebView={true} scheme={scheme}>
        <View
          activePanel={activePanel}
          popout={popout}
          modal={activeModal}
          header={false}
        >
          <Home
            id="home"
            fetchedUser={fetchedUser}
            go={go}
            updateTheme={updateTheme}
            scheme={scheme}
            course={course}
            online={online}
            shisoids={shisoids}
            getPlatform={getPlatform}
            setPopout={setPopout}
            setActivePanel={setActivePanel}
            openPopoutSharing={openPopoutSharing}
            snackbar={snackbar}
            openAlert={openAlert}
          />
           <HowItWorks id="how-it-works" go={go} />
        </View>
      </ConfigProvider>
    </Epic>
  );
};

export default App;
